@mixin web-interface__slides {
  flex-basis: 100%;
  width: 100%;

  display: flex;
  align-items: center;

  .article {
    @include article;
    position: relative;

    &__mockup {
      .mockup {
        @include mockup--placeholder;
      }
    }

    &__button {
      position: relative;
      top: -$lem * 3;
      z-index: 2;

      .button {
        @include colors(white-on-blue);
      }
    }
  }


  .slides {
    position: relative;
    top: -30vh;
    width: 100%;

    .article {
      position: absolute;
      width: 40vw;
      z-index: 1;

      &:nth-of-type(1) {
        left: -15vw;

        .article__button {
          left: 5vw;
        }
      }

      &:nth-of-type(2) {
        top: -10vh;
        left: 20vw;
        width: 60vw;
        .img {
          width: 60vw;
        }

        z-index: 2;
      }

      &:nth-of-type(3) {
        right: -15vw;

        .article__button {
          left: -5vw;
        }
      }
    }
  }


}
