@mixin publishers__mockups {
  display: flex;
  align-items: center;
  justify-content: flex-end;

  position: relative;

  .mockup {
    @include mockup($button-colors: true);

    // mobile
    &:first-of-type {
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
    }

    // browser
    &:last-of-type {
      @include mockup--placeholder($blue);

      perspective: 1000px;
      .mockup--browser {
        overflow: hidden;
        transform: rotateY(-10deg);
      }
    }
  }

  @include publishers__mockups--portrait;
  @include publishers__mockups--mobile;
  @include publishers__mockups--landscape;
  @include publishers__mockups--desktop;
}
