@mixin slider2__navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;

  position: absolute;
  width: 100%;
  // height 0, otherwise that part of the underlying image occupied by the navigation is not clickable
  height: 0;

  .button {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;

    .saquo {
      @include saquo;
    }

    &--inactive {
      opacity: .7;
      cursor: default;
    }
  }
}
