@mixin features__hero {
  @include section-fitscreen;
  min-height: auto;

  display: flex;
  flex-direction: column;
  align-items: center;

  .title {
    display: flex;
    padding: $lem;

    @include text-style(hero);
  }

  .horizontal-line {
    @include horizontal-line($lem * 10, $black);
  }

  .features__filters {
    @include features__filters;
  }
}
