@mixin device-changer__sliders {
  position: relative;

  $w: $lem * 15;
  $b: $lem * 1.5;

  .range {
    &.device-range-horizontal {
      @include range($w, $b);
      padding: 5vh 0;
    }

    &.device-range-vertical {
      @include range($w, $b, vertical);

      position: absolute;
      right: -5vh;
      top: 20vh;
    }
  }

  @include device-changer__sliders--mobile;
}
