@mixin publishers__mockups--mobile {
  @include mobile {
    .mockup {
      &:first-of-type {
        transform: scale(.4) translateX(-$lem * 5) translateY($lem * 4);

        // FIXME: Chrome bug since the @2x image is missing
        .img {
          width: 160px;
        }
      }

      &:last-of-type {
        transform: translateX($lem + $lem / 2);

        .mockup--browser {
          transform: rotateY(0deg);

          .mockup__buttons {
            padding: $lem / 6 0;

            .circle {
              @include circle($lem / 8);
            }
          }

          .mockup__iframe {
            @include mockup__iframe(70vw, 16, 9);
          }
        }
      }
    }
  }
}
