@mixin slide2 {
  position: absolute;
  left: 0;
  top: 0;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;;

  cursor: pointer;
  @include transition(transform);

  .picture {
    @include border(thin, all);
    border-color: transparent;

    padding: $lem / 4;
    line-height: 0;
  }

  &--active,
  &:hover {
    @include slide2--active;
  }
}
