@mixin hero {
  @include section-padding;
  @include section-fitscreen;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  // The height of the header must be added here, since the header has position: absolute
  margin-top: $lem * 3;

  .article {
    @include article;
    @include article--centered;

    &__title {
      @include text-style(hero);
    }

    &__subtitle {
      @include colors(gray-format-on-transparent);

      max-width: none;
      margin-top: $lem / 2;
      letter-spacing: 0.035em;
    }
  }

  .device-changer {
    @include device-changer;
  }
}
