@mixin publishers__mockups--portrait {
  @include portrait {
    justify-content: center;

    .mockup {
      &:first-of-type {
        transform: translateX(-$lem) translateY(22vh);
      }

      &:last-of-type {
        .mockup--browser {
          position: relative;
          margin-left: -$lem * 2;

          .mockup__iframe {
            @include mockup__iframe(85vw, 16, 9);
          }
        }
      }
    }
  }
}
